.table .th,
.table .td {
  padding: 5px;
  border-bottom: 1px solid #ddd;
  border-right: 1px solid #ddd;
  overflow: hidden;
}

.table .th :last-child,
.table .td :last-child {
  border-right: 0;
}

.table.sticky {
  overflow-x: scroll;
}

.table.sticky .header,
.table.sticky .footer {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.table.sticky .header {
  top: 0;
  box-shadow: 0px 3px 3px #ccc;
}

.table.sticky .footer {
  bottom: 0;
  box-shadow: 0px -3px 3px #ccc;
}

.table.sticky .body {
  position: relative;
  z-index: 0;
}

.table.sticky [data-sticky-td] {
  position: -webkit-sticky;
  position: sticky;
}

.table.sticky [data-sticky-last-left-td] {
  box-shadow: 2px 0px 3px #ccc;
}

.table.sticky [data-sticky-first-right-td] {
  box-shadow: -2px 0px 3px #ccc;
}

